.nav__button:hover .nav__icon {
  transform: scale(0.8);
  transition: transform 0.3s linear;
}

.nav__text {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.nav__icon {
  position: relative;
  top: 2px;
  width: 60px;
  height: 12px;
  display: block;
}

.nav__icon:before, .nav__icon:after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: currentColor;
  transform: scale(1);
  transition: transform 0.3s linear;
}

.nav__icon:after {
  top: auto;
  bottom: 0;
}

.nav__w {
  position: absolute;
  top: 76px;
  left: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100%);
  transition: opacity 0.3s linear;
}

@media (min-width: 700px) {
  .nav__w {
    top: 0;
  }
}

.nav__list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}

.nav__list li {
  margin-right: 30px;
}

.nav__list li:last-child {
  margin-right: 0;
}

.nav__list a {
  display: block;
  font-weight: 400;
  color: inherit;
  text-decoration: none;
  box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.4);
  transition: box-shadow 0.1s linear;
}

.nav__list a:hover {
  box-shadow: inset 0 -1px 0 transparent;
  transition: box-shadow 0.1s linear;
}

/*Modifier*/
.nav_active .nav__button:hover {
  transition: transform 0.3s linear;
}

.nav_active .nav__icon {
  transition: transform 0.3s linear;
}

.nav_active .nav__icon:after, .nav_active .nav__icon:before {
  top: 50%;
  background: #e91e63;
  transition: transform 0.3s linear;
}

.nav_active .nav__icon:after {
  transform: translateY(-50%) rotate(45deg) scale(0.8);
  transition: transform 0.3s linear;
}

.nav_active .nav__icon:before {
  transform: translateY(-50%) rotate(-45deg) scale(0.8);
  transition: transform 0.3s linear;
}

.nav_active .nav__w {
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  top: 76px;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  visibility: visible;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #151515;
  color: #fff;
  transform: translateX(0%);
  opacity: 1;
  transition: opacity 0.3s linear;
}

