.faq {
  background: #191919;
  color: #f4f4f4;
}

.faq__container {
  padding-top: 68px;
  padding-bottom: 68px;
}

.faq__title {
  margin-bottom: 50px;
  text-align: left;
}

.faq__item {
  position: relative;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding-top: 30px;
  padding-bottom: 30px;
  max-height: 90px;
  cursor: pointer;
  overflow: hidden;
}

.faq__item::after, .faq__item::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 20px;
  width: 25px;
  height: 1px;
  background: #f4f4f4;
  transform: translate(0, 0);
}

.faq__item::after {
  transform: translate(0, 0) rotate(90deg);
  transition: 0.3s ease;
}

.faq__item::before {
  transform: translate(0, 0) rotate(0deg);
  transition: 0.3s ease;
}

.faq__item:last-child {
  border-bottom: none;
}

.faq__item_active {
  padding-right: 40px;
  max-height: 100%;
}

.faq__item_active::after {
  transform: translate(0, 0) rotate(0deg);
  transition: 0.3s ease;
}

.faq__item_active::before {
  transform: translate(0, 0) rotate(0deg);
  transition: 0.3s ease;
}

.faq__item_active .faq__answer {
  visibility: visible;
  transform: translate(0, 0);
  transition: all 0.1s ease;
}

.faq__quesiton:hover {
  color: rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.faq__answer {
  padding-top: 30px;
  visibility: hidden;
  transform: translate(0, 100%);
  transition: all 0.1s ease;
}

.faq__answer p {
  margin-bottom: 15px;
}

