.link {
  color: inherit;
  @include tr(box-shadow .1s ease-out);
  &:hover {
    @include tr(box-shadow .1s ease-out); } }


.text-top {
  // color: darken($white, 60)
  color: $white;
  font-style: italic;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 12px;
  @include tracking(100);

  a {
    padding-bottom: 1px;
    display: inline-block;
    color: inherit;
    text-decoration: none;
    @include tr(box-shadow .1s ease-out);
    &:hover {
      @include tr(box-shadow .1s ease-out); } }

  @include mq($small) {
    font-size: 14px; } }

.title {
  position: relative;
  font-family: $a;
  font-size: 35px;
  font-weight: 400;
  line-height: 1.3;
  // @include tracking(20)
  text-transform: none;
  @include mq($small) {
    font-size: 56px; } }

.title_big {
  font-size: 33px;
  @include mq($small) {
    font-size: 40px; } }

.title_regular {
  font-size: 27px;
  line-height: 1.4;
  @include r($small) {
    font-size: 22px; }
  @include mq($small) {
    font-size: 33px; } }

.title_small {
  font-size: 21px;
  @include mq($medium) {
    font-size: 27px; } }


.animated-title {
  &__line {
    display: block;
    padding-bottom: 0.1em;
    white-space: nowrap;
    overflow: hidden;
    span {
      position: relative;
      display: inline-block; } } }

.text {
  position: relative;
  margin-bottom: 15px;
  font-family: $a;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;

  @include mq($small) {
    font-size: 19px; }

  h1,
  h2 {
    margin-bottom: 25px;
    font-family: $a;
    font-size: 25px;
    font-weight: 400;
    line-height: 1.3;
    text-transform: none;

    @include mq($small) {
      font-size: 46px; }
    @include mq($medium) {
      font-size: 56px; } }

  p {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0; } } }

.button {
  @include resetbutton();
  padding-top: 20px;
  padding-right: 30px;
  padding-left: 30px;
  padding-bottom: 18px;

  display: flex;
  justify-content: center;

  // background: $accent-1
  background: $gt-2;
  // background: $gray
  // color: $white
  color: $gray;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  @include tracking(50);
  // border-radius: 25px
  @include tr(all .3s ease-out);

  @include mq($small) {
    display: inline-block;
    vertical-align: top;
    font-size: 18px; }

  &:hover {
    background: linear-gradient(to right, darken(#396afc, 10), darken(#2948ff, 10));
    @include tr(all .3s ease); } }

.button_general {
  border: 2px solid $accent-1;
  background: $gt-2;
  background: transparent;
  // color: $black
  &:hover {
    background: linear-gradient(to right, darken(#396afc, 10), darken(#2948ff, 10));
    background: $accent-1;
    // color: $black
    @include tr(all .3s ease); } }

.button_small {
  padding-top: 12px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 11px;
  background: transparentize($bg, .2);
  font-size: 13px;
  color: $white;
  @include tracking(100);
  text-transform: uppercase; }

.shortlink {
  @extend .button;
  box-shadow: none; }

.flex {
  display: flex; }

.flex_start {
  justify-content: flex-start; }

.flex_center {
  justify-content: center; }

.flex_end {
  justify-content: flex-end; }

.flex_top {
  align-items: flex-start; }

.flex_stretch {
  align-items: stretch; }

.flex_vertical {
  align-items: center; }

.flex_wrap {
  flex-wrap: wrap; }

.slider {

  .slick-arrow {
    top: auto;
    bottom: 0;
    z-index: 1;
    color: $white;
    width: 100px;
    height: auto;
    transform: none;
    &:before {
      background: transparent;
      font-family: $a;
      color: transparentize($white, .5);
      font-size: 55px;
      font-weight: 400;
      font-style: italic;
      line-height: 1.454;
      @include tr(all .3s ease);

      @include mq($small) {
        font-size: 76px; } } }

  .slick-prev {
    left: 0;
    &:before {
      content: "←"; } }

  .slick-next {
    right: 0;
    &:before {
      content: "→"; } } }

.wysiwyg {
  // color: transparentize($white, .1)
  p {
    margin-bottom: $margin;
    // color: transparentize($black, .1)
    font-family: $a;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6;
    @include mq($small) {
      font-size: 19px; }
    &:last-child {
      margin-bottom: 0; } }
  h2 {
    margin-top: $margin;
    margin-bottom: $margin;
    font-size: 27px;
    @include mq($small) {
      font-size: 33px; } }

  h3 {
    margin-top: 30px;
    margin-bottom: 15px;
    font-size: 18px;
    @include mq($small) {
      font-size: 22px; } }

  a {
    color: inherit;
    text-decoration: none;
    // box-shadow: inset 0 -1px 0 transparentize($white, .2)
    @include tr(all .3s ease-out);
    &:hover {
      text-decoration: none;
      // box-shadow: none
      @include tr(all .3s ease-out); }

    &.button {
      border: 2px solid $accent-1;
      // background: $gt-2
      background: transparent;
      // color: $black
      box-shadow: none;
      &:hover {
        // background: linear-gradient(to right, darken(#396afc, 10), darken(#2948ff, 10))
        background: $accent-1;
        // color: $black
        @include tr(all .3s ease); } } }

  ul,
  ol {
    margin-bottom: $margin;
    li {
      margin-bottom: 15px;
      &:before {
        content: "— "; } }
    a {
      color: $gray;
      text-decoration: none;
      @include tr(box-shadow .1s ease-out);
      &:hover {
        @include tr(box-shadow .1s ease-out); } } }

  // ol
  //   list-style-type: decimal
  //   list-style-position: inside
  //   li
  //     &:before
  //       display: none

  strong {
    font-style: italic;
    font-weight: 700; } }

pre {
  margin-bottom: 30px;
  font-family: $j; }

code {
  padding-top: 2px;
  padding-right: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  background: transparentize($black, .95);
  font-family: $j;
  font-size: 14px;
  font-weight: normal;
  border-radius: 8px; }

