html,
body {
  height: 100%; }

body {
  background: $black;
  color: $gray;
  font-size: 16px;
  font-display: swap;
  font-family: $a;
  font-weight: 400;
  line-height: 1.4;
  @include tracking(20);
  @include mq($small) {
    font-size: 21px; } }

.container {
  margin: auto;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
  max-width: 1200px;
  @include mq($small) {
    padding-right: 50px;
    padding-left: 50px; } }

.container_full {
  max-width: 100%;
  @include r($small) {
    padding-right: 0;
    padding-left: 0; } }

.container_borders {
  position: relative; }


.code-toolbar {
  .toolbar-item {
    &:hover {
      color: transparentize($white, .1); } } }


img {
  max-width: 100%;
  height: auto; }

// a
