 *////* fade and slide transition active states (same for leave and enter) */
.fade-leave-active,
.fade-enter-active,
.slide-leave-active,
.slide-enter-active {
  transition: opacity 450ms ease, transform 650ms ease-in-out; }

/* fade transition states */
.fade-leave,
.fade-enter-to {
  opacity: 1; }

.fade-enter,
.fade-leave-to {
  opacity: 0; }

/* slide transition states */
.slide-leave,
.slide-enter-to {
  transform: translateX(0); }

.slide-enter,
.slide-leave-to {
  transform: translateX(100%); }

.wp-block-code {
  padding: 10px;
  border-radius: 10px;
  @include mq($small) {
    max-width: 80%; } }

.custom-form {
  margin-top: 100px; }


.likes {
  margin-top: 30px;
  opacity: 1;
  &_liked {
    opacity: .8; } }
.inner-block {
  color: $white;
  &__title,
  &__text {
    color: inherit; } }
